@import url(https://fonts.googleapis.com/css2?family=Karla&display=swap);
body{
  font-family: 'Karla', sans-serif;
  background: #f1faee;
}
.App{
  background: #fff;
  -webkit-animation: fadein 2s;
          animation: fadein 2s;

}
@-webkit-keyframes fadein {
  from {opacity: 0;}
  to {opacity: 1;}
}
@keyframes fadein {
  from {opacity: 0;}
  to {opacity: 1;}
}

.loader-wrapper {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: #f1faee;
  
}

.spinner {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 100vh;
}

.spinner::after {
  content: "";
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: #34495e;
  -webkit-animation: loader 1s infinite ease-in-out;
          animation: loader 1s infinite ease-in-out;
}

@-webkit-keyframes loader {
  100% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    opacity: 0;
  }
}

@keyframes loader {
  100% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    opacity: 0;
  }
}
/* Navbar */
.navbar{
    background-color: #A8DADC;
    color: #212529;
  }


/* Menu Links */
nav a.nav-link {
    letter-spacing: 0.6px;
    cursor: pointer;
  }

/* Jumbotron */
.jumbotron{
  padding-top: 15rem;
  margin-bottom: 5rem;
  }
  .jumbotron img{
    width: 200px;
  }
.about{
    padding-top: 22rem;
    margin-bottom: 20rem;
}
.kiri {
    opacity: 0;
    -webkit-transform: translate(-50px, 0);
            transform: translate(-50px, 0);
    transition: 2s;
  }
.kanan {
    opacity: 0;
    -webkit-transform: translate(50px, 0);
            transform: translate(50px, 0);
    transition: 2s;
  }
.tb {
    opacity: 0;
    transition: 1s;
}
.kiri.muncul,
.kanan.muncul,
.tb.muncul{
  opacity: 1;
  -webkit-transform: translate(0, 0);
          transform: translate(0, 0);
}

@media only screen and (max-width: 900px) {
.kiri {
    opacity: 0;
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
    transition: 2s;
  }
.kanan {
    opacity: 0;
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
    transition: 2s;
  }
}
img {
    max-width: none;
    object-fit: contain;
    width: 600px;
    height: 400px;
}

@media only screen and (max-width: 900px) {
    img {
        max-width: none;
        object-fit: contain;
        width: 300px;
        height: 200px;
    }
  }

.projects{
    padding-top: 8rem;
}
.card {
    width: 100%;
}

a {
    text-decoration: none;
}

li:hover {
    color: blue;
}

.contact {
    padding-bottom: 20rem;
    margin-top: 30rem;
}

#contact h2 {
    opacity: 0;
    transition: 2s;
}

#contact .row .card {
    opacity: 0;
    transition: 2s;
    -webkit-transform: translate(0, -20px);
            transform: translate(0, -20px);
}

#contact h2.muncul,
#contact .row .card.muncul {
  opacity: 1;
  -webkit-transform: translate(0, 0);
          transform: translate(0, 0);
}

@media only screen and (max-width: 900px) {
    #contact .row .card {
        opacity: 0;
        transition: 2s;
        -webkit-transform: translate(0, 0);
                transform: translate(0, 0);
    }
    }
